  margin-right: 0.5rem;
  transition: 0.2s ease-in-out all;
  width: 1.25rem;
  height: 1.25rem;
  display: block;
  border-radius: 0.35rem;
  border: 1px solid #dee2e6;
  transform: scale(0.9);
}
