  border-color: #3171e0;
  background: #3171e0;
  transform: scale(0.9);
}
label.input-custom-card input:checked ~ .input-wrap .input-header .indicator:before {
  color: #fff;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  opacity: 1;
  margin-top: 1.35px;
  font-size: 10px;
  text-align: center;
  transition: 0.2s ease-in-out all;
  transform: scale(1);
